<template>
  <div class="row">
    <div class="col-sm-12 col-md-8">
      <table class="table">
        <thead class="thead-default">
          <tr>
            <th>尺寸</th>
            <th>价格</th>
            <th>加入</th>
          </tr>
        </thead>
        <tbody v-for="item in getMenuInfo" :key="item.index">
          <tr>
            <td>
              <strong>{{item.name}}</strong>
              <span style="padding-left: 30px;color: #e3e4e5;">({{item.description}})</span>
            </td>
          </tr>
          <tr v-for="info in item.options" :key="info.index">
            <td>{{info.size}}寸</td>
            <td>{{info.price}}RMB</td>
            <td>
              <button @click="addBaskets(item,info)" class="btn btn-sm btn-outline-success">+</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="col-sm-12 col-md-4">
      <div v-if="ifShow">
        <table class="table">
          <thead class="thead-default">
            <tr>
              <th>数量</th>
              <th>名称</th>
              <th>价格</th>
            </tr>
          </thead>
          <tbody v-for="item in baskets" :key="item.indx">
            <tr>
              <td>
                <button @click="reduceNum(item)" class="btn btn-sm">-</button>
                <span>{{item.number}}</span>
                <button @click="addNum(item)" class="btn btn-sm">+</button>
              </td>
              <td>{{item.name}} X {{item.size}}寸</td>
              <td>{{item.price * item.number}}RMB</td>
            </tr>
          </tbody>
        </table>
        <p>总价:{{total + 'RMB'}}</p>
        <button class="btn btn-success btn-block">提交</button>
      </div>
      <div v-else>
        <p>购物车空空如也~</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ifShow: false,
      baskets: [],
      // getMenuInfo: {}    
    }
  },
  computed: {
    getMenuInfo(){
      // console.log(this.$store.state)
      return this.$store.state.menuItems;
    },
    total() {
      let totalPrice = 0;
      for (let item in this.baskets) {
        totalPrice += this.baskets[item].number * this.baskets[item].price;
      }
      return totalPrice;
    }
  },
  created() {
    this.getMenuData();
  },
  methods: {
    getMenuData(){
      //  fetch("https://my-app-8f064.firebaseio.com/menu.json")
      // .then(res => {
      //   return res.json()
      // })
      // .then(data => {
      //   this.getMenuInfo = data;
      // });
      // this.$axios.get('menu.json') 
      //     .then(res => this.getMenuInfo = res.data);

      // 将获取到的数据储存到vuex中
       this.$axios.get('menu.json') 
          .then(res => this.$store.commit('setMenuItem',res.data));
    },
    addBaskets(item, info) {
      // console.log(item,info)
      let infoList = {
        name: item.name,
        price: info.price,
        size: info.size,
        number: 1
      };

      if (this.baskets.length > 0) {
        // 过滤
        let result = this.baskets.filter(self => {
          return item.name === self.name && info.price === self.price;
        });
        if( result != null && result.length > 0){
          result[0].number++;
        }else{
          this.baskets.push(infoList)
        }
      } else {
        this.baskets.push(infoList);
      }

      if (this.baskets.length > 0) {
        this.ifShow = true;
      }
    },
    addNum(item) {
      item.number++;
    },
    reduceNum(item) {
      item.number--;
      if (item.number <= 0) {
        this.removeForm(item);
      }
    },
    removeForm(item) {
      
      this.baskets.splice(this.baskets.indexOf(item), 1);
      if (this.baskets.length <= 0) {
        this.ifShow = false;
      }
    }
  }
};
</script>

